
#app {
  width: 100%;
  height: 100%;
}
#app .notifications-container {
    position: fixed;
    top: 10px;
    right: 10px;
}
#app .loading, #app .vue-loading-msg {
    position: absolute;
    margin: 0;
    cursor: wait;
}
#app .loading {
    background-color: rgba(230, 233, 236, 0.8);
    opacity: 1;
    top: 0;
    left: 0;
    z-index: 9999;
    padding: 0;
    width: 100%;
    height: 100%;
    border: none;
    background-color: rgba(230, 233, 236, 0.8);
    -webkit-transition: opacity .4s;
    transition: opacity .4s;
}
#app .loading .vue-loading-msg {
      -webkit-box-sizing: content-box !important;
              box-sizing: content-box !important;
      z-index: 1001;
      padding: 0 35px;
      height: 40px;
      top: 20%;
      left: 50%;
      text-align: center;
      font-size: 14px;
      line-height: 40px;
      background-color: #f4f4f4;
      border-radius: 4px;
      -webkit-box-shadow: 0 1px 8px rgba(0, 0, 0, 0.15);
              box-shadow: 0 1px 8px rgba(0, 0, 0, 0.15);
      border: 1px solid #bbb;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}



