body, html {
  margin: 0;
  padding: 0;
}


.center {
  text-align: center;
}

.right {
  text-align: right;
}

.button-box {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.small {
  font-size: 80%;
}

.zero-size {
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
}

.notifications {
  .notification {
    padding: 16px;
    font-size: 16px;
  }
}



